* { 
  margin: 0; 
  padding: 0; 
} 

.navbar { 
  display: flex; 
  align-items: center; 
  justify-content: center; 
  position: sticky; 
  top: 0; 
  cursor: pointer; 
} 

.background { 
  background: rgb(255, 255, 255); 
  background-blend-mode: darken; 
  background-size: cover; 
} 

.footer { 
  background-color: #000; 
}

.preview img {
  padding-right: 50px;
  padding-left: 50px;
  padding-bottom: 50px;
  background-size: cover;
  background-position: center;
  max-width: 800px;
  width: 100%;
  height: 100%;
}

.previewText {
  padding-right: 50px;
  padding-left: 50px;
  position: relative;
}

li {
  float: left;
  display: table;
}

.mainImage img {
  display: table;
  margin: 0 auto;
  width: 100%;
}
.mainTextTheme {
  color: ghostwhite;
}

.sectionTitle {
  padding-left: 50%;
}

.nav-list { 
  width: 70%; 
  display: flex; 
  align-items: center; 
}

a {
  color: inherit;
  text-decoration: inherit;
}

ul {
  display: table;
  margin: 0 auto;
}

.logo img { 
  border-radius: 100px;
  width: 100%;
  height: 100%;
  max-width: 55px;
} 

.nav-list li { 
  list-style: none; 
  padding: 26px 30px; 
} 

.nav-list li a { 
  text-decoration: none; 
  color: #000; 
} 

.nav-list li a:hover { 
  color: grey; 
} 

.profile { 
  padding-top: 1%;
} 

.profile-text {
  color: white;
  text-transform: full-size-kana;
  padding-left: 10%;
} 

.rightnav { 
  width: 30%; 
  text-align: right; 
} 

#search { 
  padding: 5px; 
  font-size: 17px; 
  border: 2px solid rgb(0, 0, 0); 
  border-radius: 9px; 
} 

.box-main { 
  display: flex; 
  justify-content: center; 
  align-items: center; 
  color: black; 
  max-width: 80%; 
  margin: auto; 
  height: 80%; 
} 

.firsthalf { 
  width: 100%; 
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
} 

.secondhalf { 
  width: 30%; 
} 

.secondhalf img { 
  width: 70%; 
  border: 4px solid white; 
  border-radius: 150px; 
  display: block; 
  margin: auto; 
} 

.text-big { 
  font-weight: 500; 
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
  font-size: 30px;
  color: white;
} 

.text-small { 
  font-size: 18px;
  color: white; 
} 

.btn { 
margin-left: 20px; 
height: 33px; 
width: 70px; 
  color: #fff; 
background-color: #000; 
  cursor: pointer; 
} 

.btn-sm { 
  padding: 6px 10px; 
  vertical-align: middle; 
} 

.section { 
  height: 200px; 
  display: flex; 
  align-items: center; 
  background-color: rgb(33,138,255);
  justify-content: space-between; 
  border-radius: 40px; 
} 

.section-Left { 
  flex-direction: row-reverse; 
} 

.center { 
  text-align: center; 
} 

.text-footer { 
  text-align: center; 
  padding: 30px 0; 
  font-family: 'Ubuntu', sans-serif; 
  display: flex; 
  justify-content: center; 
  color: #fff; 
}

